<template>
  <div>
    <section class=titol>
      <img src="../assets/clients.png" alt="Fotografia de la portada amb el logotip"> 
    </section>
    <section class="text">
    <b-card-group columns>
      <div v-for="proyecto in proyectos" :key="proyecto.id">
      <Card2 
        :autor="proyecto.autor" 
        :srcimg="proyecto.srcimg"
        :alt="proyecto.alt"
        :titulo="proyecto.titulo" 
        :texto="proyecto.texto" 
      ></Card2> 
      </div>
    </b-card-group>
    </section>
  </div>
</template>

<script>
import Card2 from '../components/Card2.vue' 
const lstProysJson = '../blogclients.json';
  
export default {
  data() {
    return {
      proyectos:[],
    }
  },
  mounted() {
    this.fetchData()
  },
  methods: {
    async fetchData() {
      try {
        const res = await fetch(lstProysJson);
        const data = await res.json();
        this.proyectos = data;
      } catch (error) {
        console.error('Error fetching data:', error);
      }
    }
  },
  components: {
    Card2
  }
}
</script>

<style>

/*TITOL*/
.titol {
  width: 100%; 
  max-width: 1920px; 
  height: auto; 
  margin-bottom: 5%;
}
.titol img {
  width: 100%; 
  height: auto; 
  display: block; 
}

/*TEXT DE LES CARTES*/
.text{
  font-family: 'Gill Sans Light';
}
</style>
